@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin translate($x, $y) {  
    @include transform(translate($x, $y));
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
} 
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-left {
    text-align: start;
}
@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) {
    border-inline-end:$value;
}

.cart-noti-popup {
	.popup-inner {
		padding: 30px 43px;
	}
	.popup-title-inner {
		background: var(--primary-color);
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 16px;
		font-weight: 500;
		padding: 6.5px 10px;
		border-radius: var(--border-radius);
		margin: 0 auto 30px;
		max-width: 285px;
		span {
			display: flex;
		}
		.icon-ticked {
			@include margin-end(13px);
		}
	}
	.item-information {		
		align-items: center;
		product-card {
			border: 1px solid var(--miscellaneous-color);
			border-radius: var(--border-radius);
			padding: 20px;
		}
	}
	.cart-summary-content {
		margin-bottom: 20px;
	}
	.cart-summary {
		p {
			margin: 0;
		}
	}
	.cart-subtext {
		font-size: 15px;
		line-height: 1.47em;
		margin-bottom: 5px;
	}
	.cart-total {
		font-weight: 500;
		font-size: 16px;
	}
	.cart-buttons {
		margin: -5px 0;
		.button-item {
			padding: 5px 0;
		}
		.btn {
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
	.btn-cart-close {
		font-weight: 600;
		text-decoration: underline;
		text-underline-offset: 1.5px;
		min-height: 40px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.item-media {
		flex: 0 0 70px;
		max-width: 70px;
		@include margin-end(20px);
	}
	.item-information-body {
		.item-detail {
			flex: 0 0 100%;
			margin: 5px 0;
		}
		.item-quantity {
			flex: 0 0 30px;
			max-width: 30px;
			@include margin-end(10px);
		}
		.price {
			color: var(--text-color);
			font-weight: 500;
			margin: 0;
			display: block;
		}
	}
	cart-recommendation {
		display: block;
        .product-card {
        	margin-bottom: 0;
        }
	}
	.cart-recommend-container {
		margin: 20px 0;
		padding: 20px 0;
		position: relative;
		&:before {
			content: "";
			@include ab(0,-42px,n,0);
			width: calc(100% + 84px);
			height: 1px;
			background: var(--miscellaneous-color);
		}
		&:after {
			content: "";
			@include ab(n,-42px,0,0);
			width: calc(100% + 84px);
			height: 1px;
			background: var(--miscellaneous-color);
		}
	}
	.cart-recommend-title {
		font-size: 25px;
		font-weight: 500;
		line-height: 1.2em;
		margin-bottom: 20px;
	}
	&:not(.with-recommend) {
		.btn-cart-shopping, .btn-go-cart {
			background: none;
			border: 1px solid #000;
			color: #000;
		}
	}
	&.with-recommend .popup-content .item-information {
		margin-bottom: 25px;
	}
	@media (min-width: 750px) {
		product-card {
			max-width: 230px;
		}
		.card {
			margin: 0;
		}
		.cart-summary-content {
			margin-bottom: 40px;
		}
		&.with-recommend {
			.item-information {
				max-width: 420px;
				margin: 0 auto;
				.item-total-price {
					flex: initial;
					font-size: 16px;
					font-weight: 500;
				}
			}
		}
		.cart-recommend-container {
			margin: 30px 0;
			padding: 30px 0;
		}
		.cart-recommend-title {
			margin-bottom: 27px;
		}
		.item-information-body {
			.item-detail {
				flex: 1;
				margin: 0;
			}
			.item-quantity {
				flex: 0 0 50px;
				max-width: 50px;
			}
		}
	}
}